import axios from "axios";
import React, { useState } from "react";
import { useHistory } from "react-router";
import "./Login.css";

export default function LogIn({ setToken, setisAdmin }) {
  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");
  const history = useHistory();

  const changeEmail = (e) => {
    setEmail(e.target.value);
  };
  const changePass = (e) => {
    setPass(e.target.value);
  };
  const logIn = async () => {
    try {
      const res = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/logIn`, {
        email,
        pass,
      });
      setToken(res.data.token);
      console.log(res.data.token);
      // بعد ما اتاكد ان التسجيل صحيح
      //يجيب بيانات اللوق ان
      //اسوي تسجيل دخول
      history.push("/BooK");
      //أذا كان صحيح ينتقل للصفحه البوك
      if (res.data.payload.isAdmin == true) {
        setToken(res.data.token);
        console.log("token", res.data.token);
        localStorage.setItem("token", JSON.stringify(res.data.token));
// اذا كانت قيمة الادمن صحيحه  يغير قيمة التوكن 
// في اللوكل ستورج  يثبت قيمة التوكن 
//JSON.stringify تستخدم في استبدال قيمة 
        setisAdmin(res.data.payload.isAdmin);
        console.log("isAdmin", res.data.payload.isAdmin);
        localStorage.setItem(
          "isAdmin",
          JSON.stringify(res.data.payload.isAdmin)
        );
        history.push("/Home");
      } else if (res.data.payload.isAdmin == false) {
        setToken(res.data.token);
        console.log("token", res.data.token);
        localStorage.setItem("token", JSON.stringify(res.data.token));

        setisAdmin(res.data.payload.isAdmin);
        console.log("isAdmin", res.data.payload.isAdmin);
        localStorage.setItem(
          "isAdmin",
          JSON.stringify(res.data.payload.isAdmin)
        );
        history.push("/Home");
      }
    } catch (err) {
      console.log("errrror");
    }
  };
  return (
    <div className="loginbox">
      <img
        src="data:image/jpeg;base64,"
        className="imgdiv"
      />
      <h1> تسجيل الدخول </h1>
      <form>
        <p>أسم المستخدم </p>
        <input
          onChange={(e) => {
            changeEmail(e);
          }}
          type="text"
          placeholder="email"
        />
        <p>كلمة المرور </p>
        <input
          onChange={(e) => {
            changePass(e);
          }}
          type="password"
          placeholder="pass"
        />
      </form>
      <br />
      <button className="logbut"
        onClick={() => {
          logIn();
        }}
      >
        دخول
      </button>
    </div>
  );
}
